.no-page {
  width: 100%;
  min-height: 100vh;
  background-color: #000;
  color: #fff;
  overflow-x: hidden;
}

.no-page__main {
  text-align: center;
  /*padding-top: 150px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;*/
}

.no-page__title {
  text-transform: uppercase;
}

.no-page__svg {
  margin: 150px auto 50px;
  max-width: 700px;
}

.no-page__svg svg {
  margin: 0 auto;
  display: block;
  margin-top: 50px;
}
.no-page__svg path {
  transform:rotateY(-180deg) scale(0);
  transition: all 2s ease-in-out;
  transform-origin:center;
}
.no-page__svg path.processed {
  transform:rotateY(0deg) scale(1);
}

