.wpcf7-response-output.show {
  position: fixed;
  width: 450px;
  height: 250px;
  padding: 0.2em 1em;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 100%;
  max-height: 100%;
  border-color: #fff;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 1.5rem;
  font-family: 'Equinox';
  z-index: 10;
}

.wpcf7-response-output.show:before {
  content: "";
  position: fixed;
  width: 110vw;
  height: 150vh;
  background: rgba(0, 0, 0, 0.5);
  top: 50%;
  left: 50%;
  transform: translate(-51%, -60%);
  z-index: -1;
}

.wpcf7-response-output.show::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: #fff;
  width: 100%;
  height: 100%;
  z-index: -1;
  border: 4px solid #e35d14;
  transform: skewX(-10deg);
  box-shadow: 0 0 30px 2px rgb(0, 0, 0, 0.3);
}

#close {
  position: absolute;
  top: 20px;
  right: 0;
  font-size: 0;
  width: 26px;
  height: 26px;

  &::after {
    content: '\2716';
    font-size: 24px;
  }
}

.form__label-on.full {
  transform: translate(0);
}

.pages__header {
  position: sticky;
  background: #fff;
}
